import {
    LOADING_DATA,
    LOAD_SUCCESS,
    LOAD_FAILED
} from './constants';


export const loading_data = () => ({
    type: LOADING_DATA,
})

export const load_success = (data) => ({
    type: LOAD_SUCCESS,
    data
})

export const load_failed = () => ({
    type: LOAD_FAILED,
})