import { createSelector } from 'reselect'

const getData=(state)=>state['home']

const getWordFreqData = createSelector(
    getData,
    (state)=>{
        return state.get('word_freq_data')
    }
)
const getRecentGradsData = createSelector(
    getData,
    (state)=>{
        return state.get('recent_grads_data')
    }
)
const getLoading = createSelector(
    getData,
    (state)=>state.get('loading')
)

const getFailed = createSelector(
    getData,
    (state)=>state.get('failed')
)
export {
    getWordFreqData,
    getRecentGradsData,
    getLoading,
    getFailed,
}