import { call, put, takeLatest } from 'redux-saga/effects'
import { LOADING_DATA } from './constants'
import { load_success, load_failed } from './actions'
import * as d3 from 'd3'


function d3LoadData(){
   return Promise.all([
      d3.csv('./data/word_freq.csv'),
      d3.csv('./data/recent-grads.csv')
   ])
}
// worker Saga : 将在 USER_FETCH_REQUESTED action 被 dispatch 时调用
function* fetchData(action) {
   try {
      const data = yield call(d3LoadData)
      yield put(load_success(data))
   } catch (e) {
      yield put(load_failed())
   }
}

/*
  不允许并发，dispatch 一个 `USER_FETCH_REQUESTED` action 时，
  如果在这之前已经有一个 `USER_FETCH_REQUESTED` action 在处理中，
  那么处理中的 action 会被取消，只会执行当前的
*/
function* loadData() {
  yield takeLatest(LOADING_DATA, fetchData)
}

export default loadData