Home.propTypes = {
  onLoadingData: PropTypes.func.isRequired,
}

const mapStateToProps = createStructuredSelector({
  word_freq_data: state => getWordFreqData(state),
  recent_grads_data: state => getRecentGradsData(state),
  loading: state => getLoading(state),
  failed: state => getFailed(state),
})

const mapDispatchToProps = dispatch => ({
  onLoadingData: () => dispatch(loading_data())
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)

