import { fromJS } from 'immutable'
// import { packData } from 'utils/utils'

import {
  LOADING_DATA,
  LOAD_SUCCESS,
  LOAD_FAILED
} from './constants'

//the initial state of Home
const initialState = fromJS({
  word_freq_data: [],
  loading: true,
  failed: false,
  recent_grads_data: []
})

const homeReducer = (state = initialState, action) => {

  switch (action.type) {
    case LOADING_DATA:
      return state
        .set("loading", true);
    case LOAD_SUCCESS:
      return state
        .set("loading", false)
        .set("word_freq_data", action.data[0])
        .set("recent_grads_data", action.data[1])
    case LOAD_FAILED:
      return state
        .set("loading", false)
        .set("failed", true);
    default:
      return state
  }
}

export default homeReducer
